const { Title } = Typography;
type Card = Database['public']['Tables']['card']['Row'];
type Goal = Database['public']['Tables']['goal']['Row'];

const getStatus = (card: FullCard) => {
  const completed = card.goal.map(x => x.completion_dates.length == (x.quantity  || 1) ? 1 : 0) as number[];
  const sum = completed.reduce((a, b) => a+b, 0);
  return <div style={{alignItems: 'center'}}>
    <Progress percent={100*sum/(card.size*card.size)} size='small' type="circle" />
    {<span style={{fontSize: 32, verticalAlign: 'middle'}}>&nbsp;{card.title}</span>}
  </div>

}

const BingoPage = (props) => {
  const {supabase} = props;
  const [data, setData] = useState<FullCard[]>([]);
  const [selected, setSelected] = useState<string>('');

  const fetchData = async () => {
    const { data, error } = await supabase
    .from('card')
    .select('*,goal(*)')
    .order('order', { ascending: true, foreignTable: 'goal' })

  if (error) {
    console.error('Error:', error);
  } else {
    console.log('Data:', data);
  }
  setData(data);

}
  useEffect(() => {fetchData()}, []);  
    
    return <Tabs
      defaultActiveKey={data.length && data[0].title || ''}
      type="card"
      size='large'
      centered={true}
      style={{ marginBottom: 32 }}
      items={data.map((card, i) => {
        return {
          label: getStatus(card),
          key: card.title || '',
          children: <BingoTable key={i} supabase={supabase} card={card} onRefresh={fetchData}/>,
        };
      })}
    />;

};

export default BingoPage;